import { Component } from '@angular/core';
import { IonicPage, NavController, MenuController, Events } from 'ionic-angular';
import { Facebook, FacebookLoginResponse } from '@ionic-native/facebook';
import { InstagramProvider } from '../../providers/instagram/instagram';
import { CommonProvider } from '../../providers/common/common';
import { ExceptionHandlerProvider } from '../../providers/exception-handler/exception-handler';
import { UserdataProvider } from '../../providers/userdata/userdata';
import { AlertProvider } from '../../providers/alert/alert';
import { AuthProvider } from '../../providers/auth/auth';
import { AnalyticsProvider } from '../../providers/analytics/analytics';
import { TranslateService } from '@ngx-translate/core';
import { Observable } from 'rxjs';
import { FormBuilder, FormControl, FormGroup, Validators, AbstractControl } from '@angular/forms';
import { EMAIL_REGEXP } from '../../validator/emailValid';
// import { PWD_PATTERN } from '../../validator/emailValid';
import { noWhitespaceValidator } from '../../validator/profile';



@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})

export class LoginPage {
  private fbId = null;
  private email = null;
  private LoginTab: any;
  private loginDetails: any = {};
  isLoggedIn: boolean;
  public LoginDetails: any;
  private loginForm: FormGroup;
  private username: AbstractControl;
  private password: AbstractControl;

  constructor(private navCtrl: NavController,
    private facebook: Facebook, private instagramProvider: InstagramProvider,
    private commonProvider: CommonProvider, private authProvider: AuthProvider,
    private excepProvider: ExceptionHandlerProvider,
    private alertProvider: AlertProvider, private userdataProvider: UserdataProvider,
    private menuController: MenuController, private analytics: AnalyticsProvider,
    public events: Events, translate: TranslateService,
    private formBuilder: FormBuilder, ) {


    this.analytics.fa.logEvent('LoginPage');
    this.events.subscribe('fblogout', islogout => {
      this.facebook.logout().then(() => {
        console.log("successfully logged out of facebook ");
      }, err => {
        console.log("error in logging out of facebook ");
      })
    
      this.userdataProvider.isLoggedIn().subscribe(isLoggedInOrNot => {
        this.isLoggedIn = isLoggedInOrNot;
        console.log(isLoggedInOrNot, "isLoggedInOrNot");
      })
    })

    this.loginForm = this.formBuilder.group({
      username: ['', Validators.compose([Validators.required, Validators.pattern(EMAIL_REGEXP), noWhitespaceValidator])],
      password: ['', Validators.compose([Validators.required, Validators.minLength(6), Validators.maxLength(15)])]
    });
    this.username = this.loginForm.controls['username'];
    this.password = this.loginForm.controls['password'];
  }
 


  ionViewWillEnter() {

    this.menuController.swipeEnable(false);
    if (this.menuController.isOpen())
      this.menuController.close();
  }
  ionViewDidLoad() {
    this.LoginTab = 'Login';
  }


  loginWithEmail() {
    this.navCtrl.push("EmailLoginPage");
  }

  // Login with facebook
  loginWithFacebook() {
    this.facebook.login(['public_profile', 'user_friends', 'email']).then((response: FacebookLoginResponse) => {
      this.facebook.api('me?fields=id,email', []).then(profile => {
        console.log("Login response fro facebook", response);
        console.log("Profile response fro facebook", profile);
        this.fbId = { userId: profile['id'] };
        this.email = { userEmail: profile['email'] };
        this.authProvider.verifySocialId(this.fbId.userId, 'facebook').subscribe(data => {
          console.log('verifySocialId response', data)
          if (data[0].code == 200 && data[0].customer_data) {
            let userEmail = data[0].customer_data.customer[0].email;
            this.authProvider.getCustomerSocialAuth(userEmail).subscribe(res => {
              this.commonProvider.setUserData(res[0].auth_key, data);
              setTimeout(() => {
                this.loginWithGuest();
              }, 500);
            });
          } else if (data[0].code == 202) {
            if (this.email.userEmail) {
              this.authProvider.verifyEmail({ email: this.email.userEmail }).subscribe(resp => {
                this.authProvider.sendOTP({ email: this.email.userEmail }).subscribe(result => {
                  this.authProvider.verifyOtp(this.email.userEmail, { _otp: '3636' }).subscribe(res => {
                    if (res[0].code == 200 && res[0].customerexists == 'true') {
                      this.commonProvider.setUserData(res[0].auth_key, res);
                      setTimeout(() => {
                        this.navCtrl.push('LoginFullInfoPage', { email: this.email.userEmail, tempAuthToken: res[0].auth_key, social_channel: 'facebook', social_user_id: this.fbId.userId, isSocialChannelPresent: false, customerdata: res[0].customer_data.customer[0], isRegistered: true });
                      }, 3000);
                    } else if (res[0].code == 200 && res[0].customerexists == 'false') {
                      this.navCtrl.push('LoginFullInfoPage', { email: this.email.userEmail, tempAuthToken: res[0].auth_key, social_channel: 'facebook', social_user_id: this.fbId.userId, isSocialChannelPresent: false, isRegistered: false });
                    } else {
                      let toast = this.alertProvider.presentToast(res[0].message);
                      toast.present();
                    }
                    // this.navCtrl.push('EmailLoginPage', { useremail: this.email.userEmail, social_user_id: this.fbId.userId, social_channel: 'facebook', isSocialChannelPresent: false });
                  }, err => this.excepProvider.excpHandler(err));
                });
              }, err => this.excepProvider.excpHandler(err));
            } else {
              this.navCtrl.push('EmailLoginPage', { useremail: this.email.userEmail, social_user_id: this.fbId.userId, social_channel: 'facebook', isSocialChannelPresent: false });
            }
          } else {
            let toast = this.alertProvider.presentToast(data[0].message);
            toast.present();
          }
        }, err => {

        });
      }, err => {

        let toast = this.alertProvider.presentToast('Failed to get user data from Facebook');
        toast.present();
      });
    }, err => {

      let toast = this.alertProvider.presentToast(err.errorMessage);
      toast.present();
    });
  }

  // Login With Instagram //
  loginWithInstagram() {
    //returns instagram access token
    this.instagramProvider.loginViaInstagram().then((instatoken) => {
      //returns insta user info
      this.instagramProvider.getUserProfileDataViaInstagram(instatoken).subscribe(res => {
        if (res['meta'].code == '200') {
          /* verify if insta id is already present if yes save customer data and go to choose outlet 
              else go login with emailpage
          */

          this.authProvider.verifySocialId(res['data'].id, 'instagram').subscribe(data => {
            if (data[0].code == 200 && data[0].customer_data) {

              let userEmail = data[0].customer_data.customer[0].email;
              this.authProvider.getCustomerSocialAuth(userEmail).subscribe(res => {
                this.commonProvider.setUserData(res[0].auth_key, data);
                setTimeout(() => {
                  this.loginWithGuest();
                }, 500);
              });
            } else if (data[0].code == 202) {
              this.navCtrl.push('EmailLoginPage', { social_user_id: res['data'].id, social_channel: 'instagram', isSocialChannelPresent: false });
            } else {
              let toast = this.alertProvider.presentToast(data[0].message);
              toast.present();
            }
          }, err => this.excepProvider.excpHandler(err));
        }
      }, err => {
        let toast = this.alertProvider.presentToast('Failed to get user data from Instagram');
        toast.present();
      });
    }, err => {
      let toast = this.alertProvider.presentToast('Instagram login failed');
      toast.present();
    });
  }

  loginWithGuest() {
    if (this.commonProvider._noOfMalls == 1)
      this.navCtrl.setRoot('Homepage');
    else if (this.commonProvider._noOfMalls > 1)
      this.navCtrl.setRoot('SelectOutletPage');
  }
  gotoPage(page) {
    this.navCtrl.push(page);
  }


  login() {
    console.log(this.loginForm.value, "login details");
    this.authProvider.login(this.loginForm.value).subscribe(loginData => {
      console.log("login successfull", loginData);
      if (loginData['messageCode'] == '1004') {
        this.userdataProvider.login(loginData['Token'].AccessToken);
        this.userdataProvider.saveBasicDetails(this.loginForm.value.username, this.loginForm.value.password);
        if (this.isLoggedIn)
          this.navCtrl.setRoot('SelectOutletPage');
      } else {
        let toast = this.alertProvider.presentToast('Invalid email or password');
        toast.present();
      }
    }, err => {
      this.excepProvider.excpHandler(err);
    })
    
  
  }
}  



